<template>
  <div class="myorders">
     <div class="tabbar">
        <tab v-model="tab_index" prevent-default @on-before-index-change="switchTabItem" :line-width="5" active-color="#191919" defaultColor="#cccccc" bar-active-color="#191919">
            <tab-item selected @on-item-click="handler(0)">待付款</tab-item>
            <tab-item @on-item-click="handler(1)">配送中</tab-item>
            <tab-item @on-item-click="handler(2)">已完成</tab-item>
            <tab-item @on-item-click="handler(3)">退/换货</tab-item>
            <tab-item @on-item-click="handler(4)">全部</tab-item>
        </tab>
     </div>
     <!-- 待付款 -->
     <div v-if="tab_index==0" v-for="(item,index) in unpay_list" :key="index" class="unpay pad">
        <div class="clearfix three_t_m top">
            <span class="fl">订单编号：{{item.number}}</span>
            <span class="fr">取消订单</span>
        </div>
        <div class="center clearfix" @click="$router.push('/personalCenter/orderdetail')">
            <img v-for="(im,im_index) in item.img" :key="im_index" :src="im" alt="" class="fl">
            <div v-if="item.single" class="detail fl">
                <p class="two_t_h">{{item.name}}</p>
                <p class="three_t_m">{{item.specifications}}</p>
            </div>
            <span class="tip three_t_tip">待付款</span>
        </div>
        <div class="bottom clearfix">
            <span class="fl three_t_m">{{item.price}}</span>
            <button class="topay fr">去支付</button>
        </div>
     </div>
     <!-- 配送中 -->
     <div v-if="tab_index==1"  v-for="(item,index) in unpay_list" :key="index" class="distribution pad">
        <div class="clearfix three_t_m top">
            <span class="fl">订单编号：{{item.number}}</span>
            <span class="fr">取消订单</span>
        </div>
        <div class="center clearfix" @click="$router.push('/personalCenter/orderdetail')">
            <img v-for="(im,im_index) in item.img" :key="im_index" :src="im" alt="" class="fl">
            <div v-if="item.single" class="detail fl">
                <p class="two_t_h">{{item.name}}</p>
                <p class="three_t_m">{{item.specifications}}</p>
            </div>
            <span class="tip three_t_tip yellow">配送中</span>
        </div>
        <div class="bottom clearfix">
            <button class="sh ps fr">确认收货</button>
            <button class="xq ps fr">配送详情</button>
        </div>
     </div>
     <!-- 已完成 -->
     <div v-if="tab_index==2" v-for="(item,index) in unpay_list" :key="index" class="finished pad">
         <div class="clearfix three_t_m top">
            <span class="fl">订单编号：{{item.number}}</span>
            <span class="fr">删除订单</span>
        </div>
        <div class="center clearfix" @click="$router.push('/personalCenter/orderdetail')">
            <img v-for="(im,im_index) in item.img" :key="im_index" :src="im" alt="" class="fl">
            <div v-if="item.single" class="detail fl">
                <p class="two_t_h">{{item.name}}</p>
                <p class="three_t_m">{{item.specifications}}</p>
            </div>
            <span class="tip three_t_tip green">已完成</span>
        </div>
        <div class="bottom clearfix">
            <button class="pj ps fr" @click="$router.push('/personalCenter/evaluate')">评价</button>
            <button class="xq ps fr">配送详情</button>
            <button class="xq ps fr">再次购买</button>
        </div>
     </div>
     <!-- 退换货 -->
     <div v-if="tab_index==3" v-for="(item,index) in unpay_list" :key="index" class="exchange pad">
         <div class="clearfix three_t_m top">
            <span class="fl">订单编号：{{item.number}}</span>
            <span class="fr">删除订单</span>
        </div>
        <div class="center clearfix" @click="$router.push('/personalCenter/orderdetail')">
            <img v-for="(im,im_index) in item.img" :key="im_index" :src="im" alt="" class="fl">
            <div v-if="item.single" class="detail fl">
                <p class="two_t_h">{{item.name}}</p>
                <p class="three_t_m">{{item.specifications}}</p>
            </div>
            <span class="tip three_t_tip">退换中</span>
        </div>
        <div class="bottom clearfix">
            <span class="fl three_t_m">¥47.00</span>
            <button class="sh ps fr">查看详情</button>
            <button class="xq ps fr">取消退换</button>
        </div>
     </div>
     <!-- 全部 -->
     <div v-if="tab_index==4" v-for="(item,index) in unpay_list" :key="index" class="all pad">
         <div class="clearfix three_t_m top">
            <span class="fl">订单编号：{{item.number}}</span>
            <span class="fr">删除订单</span>
        </div>
        <div class="center clearfix" @click="$router.push('/personalCenter/orderdetail')">
            <img v-for="(im,im_index) in item.img" :key="im_index" :src="im" alt="" class="fl">
            <div v-if="item.single" class="detail fl">
                <p class="two_t_h">{{item.name}}</p>
                <p class="three_t_m">{{item.specifications}}</p>
            </div>
            <span v-if="item.type==2" class="tip three_t_tip green">已完成</span>
            <span v-if="item.type==4" class="tip three_t_tip grey">已取消</span>
        </div>
        <div class="bottom clearfix">
            <button v-if="item.type==2" @click="$router.push('/personalCenter/evaluate')" class="pj ps fr">评价</button>
            <button v-if="item.type==2" class="xq ps fr">配送详情</button>
            <button v-if="item.type==2||item.type==4" class="xq ps fr">再次购买</button>
        </div>
     </div>
  </div>
</template>

<script>
import { Tab, TabItem } from "vux";

export default {
  name: "Myorders",
  data() {
    return {
      tab_index: 0,
      //未付款
      unpay_list: [
        {
          single: true,
          name: "得乐蛋白五只装礼盒",
          specifications: "180g/盒；五盒/套",
          price: "¥47.00",
          img: [require("../../assets/test/goods.png")],
          number: "0123456",
          type: 2
        },
        {
          single: false,
          price: "¥69.00",
          img: [
            require("../../assets/test/goods.png"),
            require("../../assets/test/goods.png")
          ],
          number: "0223444",
          type: 4
        },
        {
          single: true,
          name: "得乐蛋白五只装礼盒",
          specifications: "180g/盒；五盒/套",
          price: "¥47.00",
          img: [require("../../assets/test/goods.png")],
          number: "0123456",
          type: 2
        },
        {
          single: false,
          price: "¥69.00",
          img: [
            require("../../assets/test/goods.png"),
            require("../../assets/test/goods.png")
          ],
          number: "0223444",
          type: 4
        },
        {
          single: true,
          name: "得乐蛋白五只装礼盒",
          specifications: "180g/盒；五盒/套",
          price: "¥47.00",
          img: [require("../../assets/test/goods.png")],
          number: "0123456",
          type: 2
        },
        {
          single: false,
          price: "¥69.00",
          img: [
            require("../../assets/test/goods.png"),
            require("../../assets/test/goods.png"),
            require("../../assets/test/goods.png")
          ],
          number: "0223444",
          type: 4
        }
      ],
      //配送中
      distribution_list: []
    };
  },
  methods: {
    handler() {},
    switchTabItem(index) {
      console.log("on-before-index-change", index);
      this.$vux.loading.show({
        text: "加载中..."
      });
      setTimeout(() => {
        this.$vux.loading.hide();
        this.tab_index = index;
      }, 800);
    }
  },
  components: {
    Tab,
    TabItem
  }
};
</script>

<style scoped lang="less">
.myorders {
  font-size: 0.16rem;
  background-color: #f2f2f2;
  padding-bottom: 0.10rem;
  .pad {
    padding: 0 0.27rem 0 0.28rem;
  }
  //待付款，配送中,已完成
  .unpay,
  .distribution,
  .finished,
  .exchange,
  .all {
    background-color: #fff;
    margin-top: 0.1rem;
    .top {
      padding: 0.15rem 0rem 0.1rem 0rem;
    }
    .center {
      padding: 0.1rem 0rem;
      border-top: 0.01rem solid #f2f2f2;
      border-bottom: 0.01rem solid #f2f2f2;
      position: relative;
      img {
        width: 0.8rem;
        height: 0.8rem;
        border-radius: 0.06rem;
        &:nth-child(2) {
          margin: 0rem 0.1rem;
        }
      }
      .detail {
        padding: 0 0 0 0.15rem;
        p {
          &:nth-child(1) {
            margin: 0.18rem 0 0.05rem 0rem;
          }
        }
      }
      .tip {
        position: absolute;
        top: 0.38rem;
        right: 0rem;
      }
      .yellow {
        color: #fcce00;
      }
      .green {
        color: #5cb531;
      }
      .grey {
          color: rgba(0,0,0,.5)
      }
    }
    .bottom {
      span {
        padding: 0.13rem 0 0.19rem 0;
      }
      .topay {
        background: #e7290f;
        border-radius: 0.03rem;
        font-family: PingFangSC-Regular;
        font-size: 0.12rem;
        color: #ffffff;
        letter-spacing: 0;
        border: none;
        padding: 0.04rem 0.12rem;
        margin: 0.1rem 0 0.15rem 0;
      }
      .ps {
        font-family: PingFangSC-Regular;
        font-size: 0.12rem;
        letter-spacing: 0;
        border-radius: 0.03rem;
        background-color: #fff;
        padding: 0.03rem 0.06rem 0.04rem 0.06rem;
        margin: 0.1rem 0 0.15rem 0.2rem;
      }
      .sh {
        color: #e7290f;
        border: 0.01rem solid #e7290f;
      }
      .xq {
        color: #7f7f7f;
        border: 0.01rem solid #7f7f7f;
      }
      .pj {
        width: 0.6rem;
        height: 0.24rem;
        background-color: #e7290f;
        color: #ffffff;
        border: none;
      }
    }
  }
}
</style>



